@import url(https://fonts.googleapis.com/css?family=Bungee+Shade&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, right top, left top, from(rgba(7, 27, 82, 1)), to(rgba(0, 128, 128, 1)));
  background: linear-gradient(to left, rgba(7, 27, 82, 1) 0%, rgba(0, 128, 128, 1) 100%);
}
h1 {
  font-family: 'Bungee Shade', cursive;
  color: #0ccac4;
}

@media(max-width: 1000px) {
  body {
    max-width: 700px !important;
    margin: 0 auto;
    display: block;
  }

  h1 {
    font-size: 2em !important;
  }

  .ma5 {
    margin: 2em !important;
  }
}
